<template lang="pug">
  .news.inner-page
    .inner-page__main
      .news__add
        news-add(user)
      .news__list(v-if="getInfo")
        div(v-infinite-scroll="onScroll" infinite-scroll-distance="50" infinite-scroll-throttle-delay="1000")
          news-block(
            v-for="feed in getFeeds"
            :key="feed.id"
            :info="feed"
            :edit="getInfo.id === feed.author.id"
            :deleted="getInfo.id === feed.author.id"
            )
    .inner-page__aside
      friends-possible
 </template>

<script>
import { mapGetters, mapActions } from 'vuex'
import FriendsPossible from '@/components/Friends/Possible'
import NewsBlock from '@/components/News/Block'
import NewsAdd from '@/components/News/Add'
import VirtualList from 'vue-virtual-scroll-list'
import infiniteScroll from 'vue-infinite-scroll'

export default {
  name: 'News',
  components: { FriendsPossible, NewsBlock, NewsAdd },
  directives: {infiniteScroll},
  data() {
    return {
      offset: 0,
      limit: 20
    }
  },
  computed: {
    ...mapGetters('profile/feeds', ['getFeeds', 'getLimit', 'getOffset']),
    ...mapGetters('profile/info', ['getInfo'])
  },
  methods: {
    ...mapActions('profile/feeds', ['apiFeeds']),
    async onScroll() {
      this.offset = this.getOffset
      this.limit = this.getLimit
      await this.apiFeeds({
        offset: this.offset,
        limit: this.limit
      })
      console.log('scroll')
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.apiFeeds()
    })
  }
}
</script>

<style lang="stylus">
@import '../../assets/stylus/base/vars.styl';

.news__add {
  margin-bottom: 30px;
}
</style>



